.Home {
    width: 100vw;
}

.Images {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.rect {
    height: 12cm;
    width: 28cm;
}

.imgDiv {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.arrow {
    height: 1cm;
    width: 1cm;
    border-radius: 50%;
    background-color: transparent;
}

.arrowButton {
    background-color: transparent;
    border: none;
    border-radius: 50%;
}

@media only screen and (min-width:1040px) and (max-width:1200px) {
    .rect {
        width: 25cm;
        height: 11cm;
    }

}

@media only screen and (min-width:950px) and (max-width:1040px) {
    .rect {
        width: 22cm;
        height: 10cm;
    }

}

@media only screen and (min-width:850px) and (max-width:950px) {
    .rect {
        width: 20cm;
        height: 9cm;
    }

}

@media only screen and (min-width:750px) and (max-width:850px) {
    .rect {
        width: 17cm;
        height: 9cm;
    }

}

@media only screen and (min-width:630px) and (max-width:750px) {
    .rect {
        width: 14cm;
        height: 9cm;
    }

}

@media only screen and (min-width:550px) and (max-width:630px) {
    .rect {
        width: 12cm;
        height: 9cm;
    }
}

@media only screen and (min-width:440px) and (max-width:550px) {
    .rect {
        width: 9cm;
        height: 8cm;
    }
}

@media only screen and (min-width:400px) and (max-width:440px) {
    .rect {
        width: 8cm;
        height: 7cm;
    }
}

@media only screen and (min-width:350px) and (max-width:400px) {
    .rect {
        width: 7cm;
        height: 6.5cm;
    }
}

@media only screen and (min-width:300px) and (max-width:350px) {
    .rect {
        width: 5.5cm;
        height: 5.5cm;
    }
}

@media only screen and (min-width:250px) and (max-width:300px) {
    .rect {
        width: 4.5cm;
        height: 5.5cm;
    }

    .arrow {
        height: 0.8cm;
        width: 0.8cm;
    }

    .heading {
        font-size: 20px;
    }
}

@media only screen and (min-width:200px) and (max-width:250px) {
    .rect {
        width: 4cm;
        height: 5cm;
    }

    .arrow {
        height: 0.5cm;
        width: 0.5cm;
    }

    .heading {
        font-size: 16px;
    }
}